.article {
  margin: 0 100px;
  // width: 100%;
  max-width: 90%;
  display: flex;
  flex-direction: column;
  align-items: center;
  margin-left: auto;
  margin-right: auto;

  @media (max-width: 900px) {
    margin: 0 40px;
  }

  @media (max-width: 400px) {
    margin: 0 10px;
  }

  .body {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    border: 1px solid #000000;
    box-sizing: border-box;
    box-shadow: 0px 0px 4px 4px rgba(0, 0, 0, 0.25);
    max-width: 850px;
    max-height: 400px;
    text-overflow: ellipsis;
    max-width: 100%;

    @media (max-width: 900px) {
      flex-direction: column;
      min-height: 400px;
    }

    .bodyLeft {
      display: flex;
      flex-direction: column;
      align-items: center;
      max-width: 50%;

      @media (max-width: 900px) {
        max-width: 100%;
      }

      .title {
        font-size: 22px;
        line-height: 26px;
        text-align: center;
        padding: 22px 40px;
        background-color: #1e252f;
        color: white;
        width: 100%;

        h3 {
          font-style: normal;
          font-weight: normal;
          font-size: 22px;
          line-height: 26px;
        }
      }

      img {
        width: 100%;
        height: 100%;
        @media (max-width: 900px) {
          display: none;
        }
      }
    }

    .bodyRight {
      display: flex;
      flex-direction: column;
      line-height: 21px;
      padding: 10px 10px;
      max-width: 55%;
      flex-grow: 1;

      @media (max-width: 900px) {
        max-width: 100%;
      }

      p {
        padding: 5px 10px;
        text-overflow: ellipsis;
        overflow: hidden;
        display: block;
      }

      pre {
        font-family: inherit;
        white-space: pre-wrap; /* css-3 */
        white-space: -moz-pre-wrap; /* Mozilla, с 1999 */
        white-space: -pre-wrap; /* Opera 4-6 */
        white-space: -o-pre-wrap; /* Opera 7 */
        word-wrap: break-word; /* Internet Explorer 5.5+ поддерживается в IE, Safari, и Firefox 3.1.*/
      }

      .time {
        margin: 10px 5px;
        padding: 0 25px;
        background-repeat: no-repeat;
        background-position: left;
      }

      .author {
        margin: 5px 5px;
        padding: 0 25px;
        background-repeat: no-repeat;
        background-position: left;
        white-space: nowrap;
      }
    }
  }
}
